@font-face {
    font-family: 'Typewriter';
    src: url(/ClickyGame/static/media/1942.ae373325.ttf) format('woff'), 
    url(/ClickyGame/static/media/1942.ae373325.ttf) format('truetype'); /* Chrome 4+, Firefox 3.5, Opera 10+, Safari 3—5 */
}

nav {
    background: #020106;
    font-family: 'Typewriter', Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.navbar-header {
    float: left;
    text-align: center;
    width: 100%;
}

.navbar-brand {
    float: none;
    color: #750705 !important;
    font-size: 4rem;
}
.img-container {
    margin: 1rem 0 0 0;
    background: #5c615d;
}

.img-thumbnail {
    width: 14rem;
    height: 11rem;
    background-color: #a9aba8;
    overflow: auto;
}
#scoreboard {
    margin-top: 1rem;
}

.btn {
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 2rem;
    margin: 0.5rem;
    border: 0.2rem #a9aba8 solid;
    padding: 0 1rem;
    background: #000000;
    color: #9f5151;
}
.character-area {
    height: 100%;
    background-color: #000030;
    display: flex;
    flex-flow: row wrap;
    padding: 20px 10%;
    justify-content: space-evenly;
    align-content: flex-start;
    overflow: visible;
}

.message-area {
    font-size: 2rem;
    text-align: center;
    color: #ffffff;
}
body {
    background-color: #000030;
    background-size: cover;
}
